.dashboard-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 16px;
  }
  
  .chart-card {
    margin-bottom: 16px;
  }
  
  .chart-title {
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 8px;
  }
  
  .tabs-list {
    display: flex;
    gap: 8px;
    margin-bottom: 16px;
  }
  
  .dropdown-container {
    margin-bottom: 16px;
  }
  
  .select-trigger {
    padding: 8px;
    border: 1px solid #d1d5db;
    border-radius: 8px;
    background-color: white;
  }
  